<template>
  <div class="hello" style="margin: 200px 100px;">
    <h1>{{ msg }}<i style="margin-left: 20px" class="el-icon-bicycle"></i></h1>
    <el-row type="flex" class="row-bg">
      <el-col :span="23"><el-input @focus="isFocus()" @blur="isBlur()" :onchange="changeButton()" placeholder="please enter your password" v-model="pwd" show-password></el-input></el-col>
      <el-col :span="1"><el-button @click="login()" :disabled="isButtonDisabled" :type="whatType" :icon="whatIcon" circle></el-button></el-col>
    </el-row>
  </div>
</template>

<script>

export default {
    name: 'HelloWorld',
    props: {
        msg: String
    },
    data(){
        return {
            pwd:'',
            isButtonDisabled:true,
            whatIcon:"el-icon-minus",
            whatType:"info",
            Focus:false
        }
    },
    methods:{
        isFocus:function(){
            this.Focus = true;
        },
        isBlur:function(){
            this.Focus = false;
        },
        changeButton: function(){
            let _this = this;
            if(_this.Focus == true) {
                if (_this.pwd != '') {
                    _this.isButtonDisabled = false;
                    _this.whatIcon = "el-icon-check"
                    _this.whatType = "info";
                } else {
                    _this.isButtonDisabled = true;
                    _this.whatIcon = "el-icon-minus"
                    _this.whatType = "info";
                }
            }
        },
        login:function () {
            let _this = this;
            this.axios.post("index/Index/login", {pwd:this.pwd})
                .then(
                    function(responser) {
                        if (responser.data.errno != 201 &&  responser.data.errno != 202) {
                            _this.isButtonDisabled = true;
                            _this.whatType = "danger";
                            _this.whatIcon = "el-icon-close";
                        } else {
                            _this.isButtonDisabled = true;
                            _this.whatType = "success";
                            _this.whatIcon = "el-icon-check";
                            // _this.axios.defaults.headers.common['Authorization'] = responser.data.token;
                            sessionStorage.setItem("Authorization",responser.data.token)
                            window.location.href = '/myworld.html';
                        }
                    }).catch(err=>{
                console.log("登录请求失败")
            })
        }
    }
}
</script>

